html {font-size: 10px}
html,
body {
	scroll-behavior: smooth;
}
body {
	display: flex;
	flex-direction: column;
	min-height: 100vh;

	font-family: var(--font-main);
	font-style: normal;
	font-weight: 400;
	font-size: 1.8rem;
	color: var(--dark);
	line-height: 1.4;
	font-optical-sizing: auto;

	background-color: var(--white);
}